<!-- src/views/About.vue -->
<template>
  <div class="about content-container">
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>
    <AboutTab
      :tab="currentTab"
      :overview="overview"
      :favorites="favorites"
    />
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import AboutTab from '@/components/AboutTab.vue'
import favoritesData from '@/assets/favorites.json'

export default {
  name: 'About',
  components: { AboutTab },
  setup() {
    const currentTab = ref('overview')
    const tabs = [
      { key: 'overview', label: 'Overview' },
      { key: 'favorites', label: 'Favorites' }
    ]
    const favorites = ref({})

    const overview = `I am an AI Safety researcher and communicator with an insatiable curiosity. An aspiring polymath, I am driven to explore everything - mathematics, governance, philosophy, economics, art, Linux, Web3, longevity, and more ... the list is endless!

Try to imagine a future where collective intelligence fuses human insight and values with AI's analytical power to solve global issues. That's the world I'm working to create. I'm not just preparing for this world; I'm actively shaping it.

A safe and good future requires deliberate effort. It doesn't just happen by default. I contribute by doing research that draws connections across disciplines, bridges knowledge gaps, and nurtures existential hope.

The best way to learn is to teach, so I constantly strive to transform everything I learn into "aha!" moments through writing and public speaking. By constant exploration and growth, I aim to develop my own unique perspective that can make a meaningful contribution.

My education blended unconventional degrees - Mathematics taught analytical rigor, Computer Science taught pratical problem-solving, and Theatre grounded me in empathy and the human element.

Professionally, as a software developer, I learned practical problem-solving; working in cybersecurity, I learned to approach risks from both defensive and offensive perspectives; and through web 3.0 development, I embraced open-source principles and explored novel governance models.

Each chapter of my life has been a transformative experience, continually reshaping my understanding of society, and technology's role in within it.

Let's connect and build an amazing future!`

    onMounted(() => {
      favorites.value = favoritesData
    })

    return {
      currentTab,
      tabs,
      overview,
      favorites
    }
  }
}
</script>

<style scoped>
@import '../assets/css/tab-view-styles.css';

.about {
  padding: var(--spacing-medium);
}
</style>
