<template>
  <div class="item-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <div class="item-title-progress">
          <p class="item-title" :style="{ marginLeft: `${indentLevel * 20}px` }">
            {{ activity.name }}
          </p>
          <div v-if="showProgress" class="progress-container">
            <div class="progress-bar">
              <div class="progress" :style="{ width: `${progressPercentage}%` }"></div>
            </div>
            <span class="progress-text">{{ progressPercentage }}%</span>
          </div>
        </div>
      </div>
      <template v-if="activity.variations">
        <ActivityItem
          v-for="variation in activity.variations"
          :key="variation.name"
          :activity="variation"
          :currentView="currentView"
          :indentLevel="indentLevel + 1"
        />
      </template>
    </template>
    
    <template v-else-if="currentView === 'list'">
      <div class="item-image-container">
        <img v-if="activity.image" :src="getImageUrl(activity.image)" :alt="activity.name" class="item-image">
        <div v-else class="item-image-placeholder">
          <font-awesome-icon :icon="['fas', getActivityIcon(activity.type)]" />
        </div>
      </div>
      <div class="item-details">
        <h3 class="item-title">{{ activity.name }}</h3>
        <div v-for="(modality, index) in activity.modalities" :key="index" class="modality-container">
          <h4 class="modality-name">{{ modality.name }}</h4>
          <p class="modality-progress">
            Progress: {{ modality.current }}/{{ modality.target }} {{ modality.unit }}
            <span v-if="modality.weight">
              (Weight: {{ modality.weight.current }}/{{ modality.weight.target }} kg)
            </span>
          </p>
          <div class="progress-container">
            <div class="progress-bar">
              <div class="progress" :style="{ width: `${(modality.current / modality.target) * 100}%` }"></div>
            </div>
            <span class="progress-text">{{ Math.round((modality.current / modality.target) * 100) }}%</span>
          </div>
        </div>
      </div>
    </template>


    <template v-else-if="currentView === 'grid'">
      <div class="grid-item-container">
        <img v-if="activity.image" :src="getImageUrl(activity.image)" :alt="activity.name" class="grid-item-image">
        <div v-else class="grid-item-image-placeholder">
          <font-awesome-icon :icon="['fas', getActivityIcon(activity.type)]" />
        </div>
        <div class="grid-item-overlay">
          <p class="grid-item-title">{{ activity.name }}</p>
        </div>
      </div>
    </template>







  </div>
</template>

<script>
export default {
  name: 'ActivityItem',
  props: {
    activity: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    },
    indentLevel: {
      type: Number,
      default: 0
    }
  },
  computed: {
    showProgress() {
      return this.activity.modalities && this.activity.modalities.length > 0;
    },
    progressPercentage() {
      if (!this.showProgress) return 0;
      const modality = this.activity.modalities[0];
      return Math.round((modality.current / modality.target) * 100);
    }
  },
  methods: {
    getImageUrl(imageName) {
      return process.env.BASE_URL + 'assets/physical/' + imageName;
    },
    getActivityIcon(type) {
      const iconMap = {
        pushup: 'dumbbell',
        pullup: 'dumbbell',
        plank: 'dumbbell',
        muscleup: 'dumbbell',
        // Add more mappings as needed
      };
      return iconMap[type] || 'question';
    }
  }
}
</script>

