<!-- src/components/ActivityTab.vue -->
<template>
  <div class="activity-tab">
    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <div class="view-toggle">
        <span>View:</span>
        <button 
          v-for="view in viewOptions" 
          :key="view.value"
          @click="changeView(view.value)"
          :class="{ active: currentView === view.value }"
        >
          <i :class="['fas', view.icon]"></i>
        </button>
      </div>
    </div>

    <template v-for="category in categories" :key="category.key">
      <h2>{{ category.label }}</h2>
      <div :class="['activity-list', currentView + '-view']">
        <ActivityItem
          v-for="activity in sortedActivities(category.key)"
          :key="activity.id"
          :activity="activity"
          :currentView="currentView"
          @click="$emit('navigate-to-activity', activity.id)"
        />
      </div>
    </template>
  </div>
</template>

<script>
import { ref  } from 'vue'
import ActivityItem from '@/components/ActivityItem.vue'

export default {
  name: 'ActivityTab',
  components: { ActivityItem },
  props: {
    tab: {
      type: String,
      required: true
    },
    activities: {
      type: Array,
      required: true
    }
  },
  emits: ['navigate-to-activity'],
  setup(props) {
    const currentSort = ref('name')
    const currentView = ref('list')

    const categories = [
      { key: 'calisthenics', label: 'Calisthenics' },
      { key: 'resistance', label: 'Resistance Training' },
      { key: 'cardio', label: 'Cardio' },
      { key: 'wellness', label: 'Wellness & Mobility' }
    ]

    const sortOptions = [
      { value: 'name', label: 'Name', icon: 'fa-sort-alpha-down' },
      { value: 'date', label: 'Date', icon: 'fa-calendar-alt' },
      { value: 'progress', label: 'Progress', icon: 'fa-tasks' }
    ]

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'list', icon: 'fa-list' },
      { value: 'grid', icon: 'fa-th-large' }
    ]

    const sortActivities = (activities) => {
      return [...activities].sort((a, b) => {
        switch (currentSort.value) {
          case 'name':
            return a.name.localeCompare(b.name)
          case 'date':
            return new Date(b.date) - new Date(a.date)
          case 'progress':
            return (b.progress || 0) - (a.progress || 0)
          default:
            return 0
        }
      })
    }

    const sortedActivities = (categoryKey) => {
      return sortActivities(props.activities.filter(activity => activity.category === categoryKey))
    }

    const changeView = (newView) => {
      currentView.value = newView
    }

    return {
      currentSort,
      currentView,
      categories,
      sortOptions,
      viewOptions,
      sortedActivities,
      changeView
    }
  }
}
</script>

<style scoped>
@import '../assets/css/tab-view-styles.css';

h2 {
  margin-top: var(--spacing-large);
  margin-bottom: var(--spacing-medium);
  color: var(--highlight-color);
}

.activity-list + h2 {
  margin-top: var(--spacing-large);
}
</style>
