<template>
	this page is generic for now, these things here dont actually represent what i like, its all biolerplate going to be replaced.
  <div class="about-tab">
    <div v-if="tab === 'overview'" class="overview">
      <p v-for="(paragraph, index) in overviewParagraphs" :key="index">{{ paragraph }}</p>
    </div>
    <div v-else-if="tab === 'favorites'" class="favorites">
      <div v-for="(items, category) in favorites" :key="category" class="favorite-category">
        <h3>{{ formatCategory(category) }}</h3>
        <div class="favorite-grid">
          <div v-for="item in items" :key="item.name" class="favorite-item">
            <div class="favorite-image">
              <img 
                v-if="item.image" 
                :src="getImageUrl(item.image)" 
                :alt="item.name" 
                @error="item.imageError = true"
                v-show="!item.imageError"
              >
              <div v-show="!item.image || item.imageError" class="favorite-image-placeholder">
                <font-awesome-icon :icon="getCategoryIcon(category)" />
              </div>
            </div>
            <div class="favorite-name">{{ item.name }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue';

export default {
  name: 'AboutTab',
  props: {
    tab: {
      type: String,
      required: true
    },
    overview: {
      type: String,
      required: true
    },
    favorites: {
      type: Object,
      required: true
    }
  },
  setup(props) {
    const overviewParagraphs = computed(() => props.overview.split('\n\n'));

    const formatCategory = (category) => {
      return category.split(/(?=[A-Z])/).join(' ');
    };

    const getImageUrl = (image) => {
      return `${process.env.BASE_URL}assets/favorites/${image}`;
    };

    const getCategoryIcon = (category) => {
      const iconMap = {
        tvShows: 'tv',
        podcasts: 'podcast',
        videoGames: 'gamepad',
        anime: 'dragon',
        cuisines: 'utensils',
        places: 'globe',
        programmingLanguages: 'code',
        artMovements: 'paint-brush',
        historicalFigures: 'landmark',
        inventions: 'lightbulb',
        hobbies: 'puzzle-piece',
        charities: 'hand-holding-heart',
        quotes: 'quote-left',
        blogs: 'rss',
        comics: 'book-open',
        fashionStyles: 'tshirt',
        mathematicalConcepts: 'square-root-alt',
        scientificTheories: 'atom',
        philosophicalConcepts: 'yin-yang',
        blockchainProjects: 'cubes'
      };
      return iconMap[category] || 'star';
    };

    return {
      overviewParagraphs,
      formatCategory,
      getImageUrl,
      getCategoryIcon
    };
  }
};
</script>


